.main-content{padding:50px 0px}.intro_section_container{display:flex;align-items:center}.intro_section_container .main-image{filter:drop-shadow(2px 4px 36px rgba(255, 255, 255, 0.168627451))}.intro-section h1{font-size:32px;color:#cfcfcf;margin-bottom:25px}.intro-section span{color:#575757}.intro-section .main-image{width:231px;height:254px}.form-section{margin-top:70px;display:flex;color:#cfcfcf;margin-bottom:30px}.form-section .gender{margin-right:44px}.form-section .gender-label{margin-bottom:22px;display:block;font-weight:700}.form-section :where(.gender-options,.search-gender-options) input{color:#cfcfcf;width:47px;height:47px;border:1px solid #242424;border-radius:11px;margin-right:10px;background-color:#0d0d0d}.search-button{padding:14px 34px;border-radius:11px;border:none;background-color:#1e1e1e;color:#cfcfcf;margin-top:50px;font-weight:700;transition:.3s ease;cursor:pointer}.search-button:hover{background-color:#313131}.border.selected{border:1px solid #276aeb !important;transition:.3s ease}.navigation a.active{font-weight:bold;color:#cfcfcf}.text-input{border-radius:11px;border:1px solid #2d2d2d;background-color:#111;padding:12px 25px;color:#fff;outline:none;height:40px;width:17%}.search-name{display:flex;flex-direction:column;gap:22px}.search-name span{color:#cfcfcf}